.background {
  background-color: #812a2a;
  padding: 50px;
}

.ingredients {
  padding-left: 60px;
  margin-top: 0px;
}

.ingredientsTitlePage {
  font-size: 50px;
  color: white;
  position: relative;
  padding-top: 0.6em;
  padding-bottom: 0.3em;

  margin: 0;
}
.ingredientsInstructions {
  padding-left: 60px;
  padding-right: 40px;
}

.ingredientsInstructions h1 {
  color: white;
  font-size: 25px;
  margin-bottom: 2px;
}
.ingredientsInstructions p {
  color: white;
  font-size: 23px;
  margin-bottom: 0px;
}

@font-face {
  font-family: "Risque";
  src: url(../fonts/Risque-Regular.ttf);
  font-weight: normal;
}

.searchBar {
  background-color: white;
  font-family: "Risque" !important;
  width: 800px !important;
  margin: 0px auto;
  font-size: 20px;
  border-radius: 20px;
  position: relative;
}

.foodItemBox {
  border-radius: 12px;
  background-color: white;
  padding: 20px;
  color: black;
  width: 120px;
  margin-bottom: 3em;
  position: relative;
  height: 170px;
}

.foodItemBoxSelected {
  border: solid 6px black;
  border-radius: 12px;
  background-color: white;
  padding: 20px;
  color: black;
  width: 120px;
  margin: -6px -6px calc(3em - 6px) -6px;
  position: relative;
}

.foodItemBox:hover {
  cursor: pointer;
}

.highlightFoodItemBox {
  background-color: green;
}

.foodItemImage {
  width: auto;
  max-width: 90%;
  height: 100px;
}

@keyframes animate {
  0% {
    transform: translateY(0) rotate(0deg);
    opacity: 1;
    border-radius: 90%;
  }

  100% {
    transform: translateY(1000px) rotate(720deg);
    opacity: 0;
    border-radius: 90%;
  }
}

.search {
  width: 100%;
  position: relative;
  display: flex;
}

.searchTerm {
  width: 87%;
  border-right: none;
  padding: 5px;
  height: 22px;
  border-radius: 5px 0 0 5px;
  outline: none;
  color: #9dbfaf;
}

.searchTerm:focus {
  color: rgba(134, 5, 5, 0.877);
}

.searchButton {
  width: 100px;
  height: 36px;
  border: 1px solid rgba(134, 5, 5, 0.877);
  background: white;
  text-align: center;
  color: rgba(134, 5, 5, 0.877);
  border-radius: 0 5px 5px 0;
  cursor: pointer;
  font-size: 20px;
}

.generate button {
  width: 300px;
  font-size: large;
}

.generate button:hover {
  width: 300px;
  font-size: large;
  background-color: black;
  color: white;
}
